<template>
  <div>
    <div
      class="table"
      :style="{
        width:tableSize.width+'px',
        height:tableSize.height+'px',
        fontSize: setTableFontSize
      }"
    >
      <div v-if="playingField.length">
        <TableCell
          v-for="cell in playingField"
          :key="cell.value"
          :cell="cell"
          :userAnswerAction="userAnswer"
          :isResizing="isResizing"
        ></TableCell>
      </div>

    </div>
  </div>
</template>

<script>
import { mapGetters, mapActions } from "vuex";
import TableCell from './TableCell.vue';

export default {
  name: "Table",
  components: {
    TableCell
  },
  props: ["isResizing"],
  data(){
    return {}
  },
  beforeCreate(){
    this.$store.dispatch('config/calcTableDimension')
  },
  beforeMount() {
    //console.log(this);
    this.$store.dispatch('game/startGame')
  },
  computed: {
    ...mapGetters({
      tableSize: "config/tableSize",
      playingField: "game/playingField",
      userAnswer: "game/userAnswer",
      tableSelectedSize: 'config/tableSelectedSize',
    }),
    setTableFontSize(){
      if(this.tableSelectedSize <= 7){
        return '2.5rem'
      }else if(this.tableSelectedSize <= 11){
        return '1.7rem'
      } else {
        return '1.2rem'
      }
    }
  },
  methods:{
    ...mapActions({

    })
  }
};
</script>

<style scoped>

.table {
  position: relative;
  margin-bottom: 10px;
  transition: opacity ease .3s;
}


</style>