import * as types from '../mutation-types';
import { translateTimerVal } from '../../services/helpers'
const state = {
    timerHolder:null,
    timer:0,
}

const mutations = {
    [types.STATS_START_TIMER](state) {
        state.timerHolder = setInterval(() => {
            state.timer+=100;
        }, 100)
    },
    [types.STATS_END_TIMER](state) {
        clearInterval(state.timerHolder)
    },
    [types.STATS_RESET_TIMER](state) {
        clearInterval(state.timerHolder)
        state.timerHolder = null;
        state.timer = 0;
    }
}

const actions = {
    commitTimeMark({ commit, dispatch, state, rootState }){
        //if no timer - init timer
        if(!state.timerHolder){
            commit(types.STATS_START_TIMER)
        }
    },
    resetTimeMark({commit}){
        commit(types.STATS_RESET_TIMER)
    }
}

const getters = {
    timerObj : state => translateTimerVal(state.timer)
}

const statsModule = {
    namespaced: true,
    state,
    mutations,
    actions,
    getters
}

export default statsModule;
