<template>
  <div class="menu" >
    <div class="menu__toggle-button" @click="expandMenu"  :class="{ 'active': isExpanded }">
      <IconMenuArrow/>
    </div>
    <div class="menu__holder" :class="{ 'active': isExpanded }">

      <div class="menu__timer">
        <span>{{timerObj.hours}}</span>:<span>{{timerObj.minutes}}</span>:<span>{{timerObj.seconds}}</span>:<span>{{timerObj.milliseconds}}</span>
      </div>

      <div class="menu__right-unswer" :class="{'hide':nextRightValue === null}">
        The {{nextRightValue  === 1 ? 'first' : 'next'}} choice should be <b>"{{nextRightValue}}"</b>
      </div>

      <div class="menu__title">
        <span>Options</span>
      </div>

      <TableSizes/>
      <br>
      <v-button @click="shakeGameField" markup="inverse" class="menu__reset-button">
        <IconMenuReset/>
        <span>Reset</span>
      </v-button>
    </div>
  </div>
</template>

<script>
import { mapGetters, mapActions } from "vuex";
import TableSizes from "./Table/TableSizes.vue";
import Button from './ui/Button.vue';
import IconMenuArrow from './ui/icons/IconMenuArrow.vue';
import IconMenuReset from './ui/icons/IconMenuReset.vue'

export default {
  name: "Menu",
  components: {
    IconMenuReset,
    IconMenuArrow,
    TableSizes,
    "v-button":Button,
  },
  data() {
    return {
      isExpanded:false,
    };
  },
  computed: {
    ...mapGetters({
      timerObj: "stats/timerObj",
      nextRightValue: "game/nextRightValue"
    })
  },
  methods: {
    ...mapActions({
      shakeGameField:'game/shakeGameField'
    }),
    expandMenu(){
      this.isExpanded = !this.isExpanded;
    }
  }
};
</script>

<style scoped lang="scss">
  @import '../style/variables.scss';

  .menu{position: absolute;right:0;top:0;

    &__holder{position: absolute;right:0;top:0;transition:all ease .3s;transform: translateY(-100%);width: 310px;background: $lightGrey;display: flex;flex-direction: column;padding: 15px 30px 20px;z-index: 101;border-radius: 8px;
      &.active{transform: translateY(0);}
    }
    &__toggle-button{position: fixed;top:0;right:0;z-index: 102;width: 50px;height: 50px;background: $green;border-radius: 7px 0px 7px 7px;display: flex;align-items: center;justify-content: center;cursor:pointer;
      &.active svg{transform: rotate(0deg);}
      svg{transform: rotate(-180deg);transition:all ease .3s;}
    }
    &__reset-button{
      &:active{
        svg{animation:spin .5s linear 1;}
      }
      @keyframes spin { 100% { transform: rotate(359deg); } }
    }
    &__timer{font-weight: 600;color: #B1B1B1;font-size: 20px;line-height: 1.2;
      span:last-child{color:$green;}
    }
    &__title{font-size: 20px;margin:0px 0px 30px;}
    &__right-unswer{font-size: 17px;margin:20px 0px;
      &.hide{opacity:0;}
      b{color:$green;}
    }
  }

</style>