<template>
  <button
    class="v-btn"
    :id="id"
    :class="customClass"
    :name="name"
    :disabled="disabled"
    :small="small"
    :markup="markup"
    @click="handleClick"
  >
    <slot></slot>
  </button>
</template>

<script>
export default {
  name: 'v-button',
  props: {
    id: {
      type: String,
      default: null
    },
    customClass: {
      type: String,
      default: null
    },
    name: {
      type: String,
      default: null
    },
    disabled: {
      type: Boolean,
      default: false
    },
    small: {
      type: Boolean,
      default: false
    },
    markup: {
      type: String,
      default: 'default'
    }
  },
  methods: {
    handleClick (e) {
      this.$emit('click', e)
    }
  }
}
</script>

<style lang="scss" >
@import '../../style/variables.scss';

.v-btn {cursor:pointer;width: 100%;max-width:250px;font-size: 16px;line-height: 1.2;margin:0 0 10px;padding: 15px 10px;border-radius:7px;background: #fff;border: none;text-decoration: none;outline: 0;transition:all ease .2s;

  &:disabled {cursor: not-allowed;color: $disabled-color;box-shadow: 0 0 0 1px $disabled-color inset;}

  &[small="true"] {max-width: 120px;}

  &[markup="default"] {}

  &[markup="active"],.active {color:white;background: $green;}
  &[markup="inverse"] {background: $green;color:white;display: flex;align-items: center;justify-content: center;
    .svg-path{fill:#fff;transition:all ease .2s;margin:0 5px;}
    &:hover{background: white;color: $green;
      .svg-path{fill:$green;}
    }

  }

}
</style>