<template>
  <div class="table-holder">
    <vue-draggable-resizable
      :w="tableDemensions.width"
      :h="tableDemensions.height"
      :x="x"
      :y="y"
      @dragging="onDrag"
      @resizing="onResize"
      @resizestop="onResizeStop"
      :parent="true"
      :min-width="tableVariabilitySizes.minWidth"
      :min-height="tableVariabilitySizes.minHeight"
      :max-width="tableVariabilitySizes.maxWidth"
      :max-height="tableVariabilitySizes.maxHeight"
      class-name="table-resizer"
      class-name-resizing="table-resizer-resize"
    >
      <Table :isResizing="isResizing" />
    </vue-draggable-resizable>
  </div>
</template>

<script>
import { mapGetters, mapActions } from "vuex";
import VueDraggableResizable from "vue-draggable-resizable";
import "vue-draggable-resizable/dist/VueDraggableResizable.css";

import Table from "./Table.vue";


export default {
  name: "TableResizer",
  components: {
    "vue-draggable-resizable": VueDraggableResizable,
    Table,
  },
  data: function() {
    return {
      isResizing:false,
      x: 0,
      y: 0
    };
  },
  created: function () {
    this.centerTable();
    window.addEventListener('resize', this.handleResize)
  },
  beforeDestroy: function () {
    window.removeEventListener('resize', this.handleResize)
  },
  mounted(){
    console.log(this)

  },
  methods: {
    onResize: function(x, y, width, height) {
      this.isResizing = true;
      this.x = x;
      this.y = y;
      this.$store.dispatch('config/updateTableDimension',{width,height})
    },
    onResizeStop: function (x, y, width, height) {
      this.isResizing = false;
    },
    onDrag: function(x, y) {
      this.x = x;
      this.y = y;
    },
    centerTable(){
      const windowWidth = window.innerWidth;
      const windowHeight = window.innerHeight;
      const {height:tableHeight,width:tableWidth} = this.tableDemensions;
      const positionLeft = (windowWidth - tableWidth)/2;
      const positionTop = (windowHeight - tableHeight)/2;
      this.x = positionLeft;
      this.y = positionTop;
    },
    handleResize (event) {
      this.centerTable();
    }
  },
  computed: {
    ...mapGetters({
      tableDemensions:"config/tableSize",
      tableVariabilitySizes:"config/tableVariabilitySizes",
    })
  }
};
</script>

<style lang="scss">
@import '../../style/variables.scss';

.table-holder {width: 100%;height: 100%;flex: 1;margin: 0 auto;display: flex;flex-direction: column;align-items: center;border:1px solid $green;position: relative;}
.table-resizer{border:none;}
.table-resizer-resize{
  .table{opacity: .6;}
}
.handle{background: none;border:none;z-index: 12;
  &:after{content: '';width: 0;height: 0;border-style: solid;border-width: 0 4px 10px 4px;border-color: transparent transparent $green transparent;position: absolute;left: 1px;top: -1px;}
  &:before{content: '';width: 0;height: 0;border-style: solid;border-width: 0 4px 3px 4px;border-color: transparent transparent #fefffe transparent;position: absolute;left: 1px;top: 6px;z-index: 2;}
  &.handle-tl{transform: rotate(-45deg);}
  &.handle-tm{transform: translateY(-2px)}
  &.handle-tr{transform: rotate(45deg);}
  &.handle-mr{transform: rotate(90deg) translateY(-2px);}
  &.handle-br{transform: rotate(135deg);}
  &.handle-bm{transform: rotate(180deg) translateY(-2px);}
  &.handle-bl{transform: rotate(-135deg);}
  &.handle-ml{transform: rotate(-90deg) translateY(-2px);}
}
</style>