<template>
    <ul class="table-size-holder">
        <v-button
            v-for="(size, index) in tableSizes"
            :key="index"
            :markup="size === tableSelectedSize ? 'active' : ''"
            class="table-size-holder__item"
            @click="updateTableSize(size)"
            small
        >
            <span class="table-size-holder__text">{{size}}x{{size}}</span>
        </v-button>
    </ul>
</template>

<script>
import { mapGetters, mapActions  } from 'vuex';
import Button from '../ui/Button.vue';

export default {
  name: 'TableSizes',
  components: {
    "v-button":Button
  },
  computed: {
    ...mapGetters({
      tableSelectedSize: 'config/tableSelectedSize',
      tableSizes: 'config/tableSizes',
    })
  },
  methods: {
    ...mapActions({
      updateTableSize: 'config/updateTableSize'
    })
  }
}
</script>

<style scoped lang="scss">
@import '../../style/variables.scss';

.table-size-holder{list-style-type: none;padding:0;margin:0;display: flex;justify-content: center;flex-wrap: wrap;margin:0 -5px;
  &__item{margin:0 5px 10px;}
  &__text{}
}
</style>