export function pad(deg){
    return ('0' + deg).slice(-2)
}

export function translateTimerVal(timer){
    return {
        milliseconds : pad(parseInt((timer % 1000) / 100)),
        seconds : pad(Math.floor((timer / 1000) % 60)),
        minutes : pad(Math.floor((timer / (1000 * 60)) % 60)),
        hours : pad(Math.floor((timer / (1000 * 60 * 60)) % 24))
    }
}